import React, { useState, useEffect } from 'react'
import acat from '../../../req/requests'
import axios from 'axios'
import { message, Input, Modal, Button, Form, Checkbox } from 'antd'

const inputStyle = { border: 'none', borderBottom: '1px solid rgba(0,0,0,.6)', paddingLeft: '5px', borderRadius: 'none' };
const btnStyle = { backgroundColor: '#0067b8', color: 'white', padding: '5px 33px' };
const disStyle = { backgroundColor: 'rgba(8, 103, 184,.5)', color: 'white', padding: '5px 33px' }

interface SignInProps {
    goUp: Function,
    setLogin: any,
}

const SignIn: React.FC<SignInProps> = (props) => {
    let [userid, setId] = useState('');
    let [pass, setPass] = useState('');
    let [email, setMail] = useState('');
    let isDisabeld = !userid || !pass;
    function login() {
        acat.login({
            data: {
                userid,
                password: pass,
            }
        }).then(() => {
            let { data, code, msg } = acat.getData('login');
            if (!code) {
                const { Token, userInfo } = data;

                if (userInfo && userInfo.auth === 1) {
                    return message.info('权限不足');
                }

                localStorage.setItem('access_token', Token);

                userInfo && localStorage.setItem('user-info', JSON.stringify(userInfo));

                axios.defaults.headers = Object.assign(axios.defaults.headers, {
                    'access_token': Token
                });

                props.setLogin(true);

            }
            message.info(msg);
        })
    }

    let [isPassEdit, setPassEdit] = useState(false);
    let [hasReset, setReset] = useState(false);
    const onFinish = (values: any) => {
        let { userid, pass, verifyCode } = values;
        const data = { userid, new_password: pass, verifyCode };
        console.log(data);
        acat.resetPass({
            data
        }).then(() => {
            let { code, msg } = acat.getData('resetPass');
            if (!code) {
                message.success(msg);
                setReset(true);
            } else {
                message.error(msg);
            }
        });
    };

    const onFinishFailed = () => {
        message.info('请输入完整的表单信息');
    };


    let [codeclick, setCodeClick] = useState(true);

    const sendEmail = () => {
        // 限制多次点击
        if (!codeclick) {
            return;
        }
        // 验证格式
        const emailReg = /^[\w\d]{1,}@[a-zA-Z]{1,}\.com$/;
        if (!emailReg.test(email)) {
            setMail('');
            return message.info('邮箱格式错误');
        }
        setCodeClick(false);
        // 请求验证码
        let timer: NodeJS.Timer;
        acat.sendVeriCode({
            data: {
                email,
                action: '找回密码',
            }
        }).then(() => {
            let { code, msg } = acat.getData('sendVeriCode');
            if (!code) {
                timer = setInterval(() => {
                    if (codeCount <= 0) {
                        setCount(60);
                        setCodeClick(true);
                        return clearInterval(timer);
                    } else {
                        codeCount--;
                        setCount(codeCount);
                    }
                }, 1000);
                message.success(msg);
            } else {
                setCodeClick(true);
                message.error(msg);
            }
        }).catch(() => {
            setCodeClick(true);
        })
    }

    let [codeCount, setCount] = useState(60);

    const codeBtnDis = !email || codeCount !== 60 || !codeclick;
    const codeText = codeCount !== 60 ? `(已发送)${codeCount}S` : '发送验证';

    return (
        <div className='sign-in'>
            {/* 头部 */}
            <div className="heading bolder_name">
                登录
            </div>
            <div className="loginDescription">
                使用你的acat账户登录以进行2022届纳新面试管理。
            </div>

            {/* 输入框部分 */}

            <Input placeholder='输入你的学号' className='item_input' onChange={(e) => { setId(e.target.value.trim()) }} style={inputStyle} />
            <Input placeholder='输入你的密码' className='item_input' onPressEnter={isDisabeld ? () => { } : login} onChange={(e) => { setPass(e.target.value.trim()) }} style={inputStyle} type='password' suffix={
                (<span className='jump-text' onClick={() => { setPassEdit(true) }}>忘记密码？</span>)
            } />

            {/* 底部跳转和登录按钮 */}
            <div className="btn-wrapper">
                <span className='jump-text' onClick={() => {
                    props.goUp();
                }}>没有账号？注册一个 </span>
                <Button type='primary' disabled={isDisabeld} style={!isDisabeld ? btnStyle : disStyle} onClick={login}>登录</Button>
            </div>

            {/* 找回密码的模块 */}
            <Modal title='找回密码' okText='确认' cancelText='取消' visible={isPassEdit} onCancel={() => {
                setPassEdit(false);
            }} onOk={() => {
                if (!hasReset) {
                    message.info('你还没有找回密码');
                }
                setPassEdit(false);
            }}>
                <Form
                    name="basic"
                    labelCol={{ span: 5 }}
                    wrapperCol={{ span: 16 }}
                    initialValues={{ remember: true }}
                    onFinish={onFinish}
                    onFinishFailed={onFinishFailed}
                    autoComplete="off"
                >
                    <Form.Item
                        label="用户名"
                        name="userid"
                        rules={[{ required: true, message: '输入用户名!' }]}
                    >
                        <Input />
                    </Form.Item>

                    <Form.Item
                        label="注册邮箱"
                        name="email"
                        rules={[{ required: true, message: '输入你注册时的邮箱!' }]}
                    >
                        <div className="mail-send">
                            <Input onChange={(e) => { setMail(e.target.value) }} />
                            <Button
                                onClick={sendEmail}
                                disabled={codeBtnDis}
                                style={{
                                    ...(codeBtnDis ? disStyle : btnStyle),
                                    marginLeft: '10px'
                                }}
                            >
                                {codeText}
                            </Button>
                        </div>
                    </Form.Item>

                    <Form.Item
                        label="验证码"
                        name="verifyCode"
                        rules={[{ required: true, message: '输入邮箱验证码!' }]}
                    >
                        <Input />
                    </Form.Item>

                    <Form.Item
                        label="新密码"
                        name="pass"
                        rules={[{ required: true, message: '输入新密码!' }]}
                    >
                        <Input />
                    </Form.Item>

                    <Form.Item wrapperCol={{ offset: 16, span: 16 }}>
                        <Button type="primary" htmlType="submit" style={btnStyle}>
                            修改
                        </Button>
                    </Form.Item>
                </Form>
            </Modal>
        </div>

    )
}

export default SignIn