import { Menu, Button, message, Modal, Form, Input, Select } from 'antd'
import React, { Component, ReactNode, RefObject } from 'react'
import acat from '../../req/requests'
import './index.css'
import Interview from './Interview/Interview'
import { wishMapper } from './Interview/littlePiece/funcs'
import ShouQuan from './ShouQuan/ShouQuan'
import Userinf from './Userinf/Userinf'
import QRCode from 'qrcode.react'
interface Props {
    setLogin: any
}
export default class Index extends Component<Props, object> {
    items = [
        {
            label: '开始面试',
            key: 'menu1',
        },
        {
            label: '授权管理',
            key: 'menu2',
        },
        {
            label: '个人信息',
            key: 'menu3',
        },
    ]
    state: Readonly<{ key: string }> = { key: 'menu1' }

    selectHandler = (props: any) => {
        let { key } = props;
        this.setState({
            key
        })
    }

    getWish = () => {
        const userInf = JSON.parse(localStorage.getItem('user-info') as string);
        if (userInf && userInf.wish && userInf.name) {
            return;
        }
        let name = '',
            wish = '';
        if (userInf && userInf.wish) {
            wish = userInf.wish;
        }
        if (userInf && userInf.name) {
            wish = userInf.name;
        }
        Modal.info({
            title: '填入你作为面试官的必要信息',
            content: (
                <Form>
                    <Form.Item
                        label="姓名"
                        name="username"
                        rules={[{ required: true, message: 'Please input your name!' }]}
                    >
                        <Input onChange={(e) => { name = e.target.value }} />
                    </Form.Item>
                    <Form.Item
                        label='组别'
                        name='wish'
                        rules={[{ required: true, message: 'Please input your group!' }]}
                    >
                        <Select onChange={(value) => { wish = value; }} placeholder='选择你职责所在的组别' options={[
                            {
                                label: wishMapper(4),
                                value: 4
                            },
                            {
                                label: wishMapper(1),
                                value: 1
                            },
                            {
                                label: wishMapper(2),
                                value: 2
                            },
                            {
                                label: wishMapper(3),
                                value: 3
                            }
                        ]} />
                    </Form.Item>
                </Form>
            ),
            onOk(close) {
                if (name && wish) {
                    console.log(name, wish);
                    acat.updateInf({
                        data: {
                            name,
                            wish
                        }
                    }).then(() => {
                        const { code, msg } = acat.getData('updateInf');
                        message[code ? 'warn' : 'success'](msg);
                        localStorage.setItem('user-info', JSON.stringify({
                            name,
                            wish
                        }))
                    });
                    return close();
                }
            },
            okText: '确认'
        });
    }

    mountLimit = true;
    componentDidMount(): void {
        if (this.mountLimit) {
            this.mountLimit = false;
            return;
        }
        this.getWish();
    }

    showQrcode() {
        Modal.info({
            title: '签到二维码',
            content: (
                <div>
                    <div className='qrcode-wrapper'>
                        <QRCode
                            id="qrCode"
                            value="http://192.168.1.5:10086/#/pages/index/index?type=sign"
                            size={200} // 二维码的大小
                            fgColor="#000000" // 二维码的颜色
                            style={{ margin: 'auto' }}
                            imageSettings={{ // 二维码中间的logo图片
                                src: require('../../assets/img/logo.webp'),
                                height: 45,
                                width: 45,
                                excavate: true, // 中间图片所在的位置是否镂空
                            }}
                        />
                    </div>
                    <div className='cheng'></div>
                </div>
            ),
            okText:'关闭'
        });
    }

    render() {
        return (
            <div className='index-page'>
                <div className="index-main shadow-box">
                    <div className="head">
                        <div className="logo">
                            <img src={'https://cdn.newimg.ltd/recruitSystem/img/logo.jpeg'} alt="" />
                            <span className='logo-text'>一只猫面试管理</span>
                        </div>
                        <div className="menu-wrapper">
                            <Menu onSelect={this.selectHandler} mode='horizontal' items={this.items} defaultSelectedKeys={['menu1']} />
                            <img src={require('../../assets/img/QRcode.png')} className='qr-icon' onClick={this.showQrcode} />
                        </div>
                        <div className="signout">
                            <Button onClick={() => {
                                this.props.setLogin(false);
                                localStorage.removeItem('access_token');
                                message.info('退出登录');
                            }}>登出</Button>
                        </div>
                    </div>
                    <div className="content-wrapper">
                        {
                            ((): ReactNode => {
                                let resNode: ReactNode;
                                let { key } = this.state;
                                switch (key) {
                                    case 'menu1':
                                        resNode = (<Interview setLogin={this.props.setLogin} />)
                                        break;
                                    case 'menu2':
                                        resNode = (<ShouQuan />)
                                        break;
                                    case 'menu3':
                                        resNode = (<Userinf />)
                                        break;
                                    default:
                                        break;
                                }
                                return resNode;
                            })()
                        }
                    </div>
                </div>
            </div>
        )
    }
}
